<template>
  <div class="login">
    <van-nav-bar>
      <template #left>
        <div class="logo"></div>
        <div class="logo-title">Luckin Coffee</div>
      </template>
      <template #right>
        <div @click="goHome">
          <van-icon name="ellipsis" class="right-icon" />
        </div>
      </template>
    </van-nav-bar>

    <div class="login-form">
      <div class="login-title">欢迎回来!</div>
      <div class="login-text">
        Plessw login to your
        <br />accounts
      </div>
    </div>

    <van-form class="van-form">
      <van-field
        class="login-inp"
        name="用户名"
        label="账号"
        placeholder="手机号/邮箱"
        v-model="userLoginInfo.phone"
      />
      <van-field
        class="login-inp"
        :type="isopen ? 'password' : 'text'"
        name="密码"
        label="密码"
        v-model="userLoginInfo.password"
        placeholder="密码"
        :right-icon="isopen ? 'closed-eye' : 'eye-o'"
        @click-right-icon="OpenPassword"
      />
      <div class="clearfix forgetpassword">
        <span class="fr" @click="goforget">忘记密码？</span>
      </div>
      <div class="login-btn">
        <van-button round block type="info" native-type="submit" @click="login">登录</van-button>
      </div>

      <div class="login-box">
        <van-cell title="注册" @click="show = true" />

        <van-action-sheet v-model="show" title="用户注册" class="login-show">
          <van-field placeholder="1-10位昵称" v-model="userRegisterInfo.nickName" />
          <van-field v-model="userRegisterInfo.phone" type="tel" placeholder="手机号 中国 (+86)" />
          <van-field v-model="userRegisterInfo.password" type="password" placeholder="密码" />
          <van-button type="primary" block class="login-show-btn" @click="register">注册</van-button>
        </van-action-sheet>
      </div>
    </van-form>
  </div>
</template>
<script>
// 导入css文件
import "../assets/less/login.less";

// 导入JS文件
import { validForm } from "../assets/js/validForm";

import { utils } from "../assets/js/utils";

export default {
  data() {
    return {
      // 登录数据
      userLoginInfo: {
        phone: "",
        password: "",
      },
      // 注册数据
      userRegisterInfo: {
        nickName: "",
        phone: "",
        password: "",
        // loginpassword2: "",
      },
      isopen: true,
      show: false,
    };
  },
  methods: {
    // 返回首页
    goHome() {
      this.$router.push({name: 'Home'});
    },
    // 忘记密码
    goforget(){
      this.$router.push({name:"Forgot"})
    },
    //查看密码
    OpenPassword() {
      this.isopen = !this.isopen;
    },
    // 弹出注册面板
    showPopup() {
      this.show = true;
    },
    // 用户登录处理

    // 注册面板处理
    register() {
      // 获取用户注册信息
      let userInfo = this.userRegisterInfo;
      

      // 验证注册表单
      let o = {
        phone: {
          value: userInfo.phone,
          reg: /^1[3-9]\d{9}$/,
          errorMsg: "手机号格式不正确",
        },
        nickName: {
          value: userInfo.nickName,
          reg: /^[\w\u4e00-\u9fa5]{1,10}$/,
          errorMsg: "昵称格式不正确",
        },
        password: {
          value: userInfo.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          errorMsg: "密码格式不正确",
        },
      };
      if (!validForm.valid(o)) {
        // 验证不通过
        return;
      }

      // 发起请求
      // 开启加载提示
      // 自定义加载图标
      this.$toast.loading({
        message: "加载中...",
        // 防止穿透
        forbidClick: true,
        // 显示时间,如果时间为0,则不会自动关闭
        duration: 0,

        loadingType: "spinner",
      });

      // 序列化参数
      //复制对象
      let info = Object.assign({}, userInfo);

      info.appkey = this.appkey;
      
      let data = utils.queryString(info);

      this.axios({
        method: "POST",
        // 请求参数
        url: "/register",
        data,
      })
        .then((result) => {
          
          // 请求成功
          // 关闭加载提示
          this.$toast.clear();

          // 如果注册成功
          if (result.data.code == 100) {
            this.show = false;
            // 清空注册表单的数据
            for (const key in userInfo) {
              userInfo[key] = "";
            }
          } else {
            // 如果注册失败
            this.$notify({
              type: "warning",
              message: result.data.msg,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    // 登录
    login() {
      let userInfo = this.userLoginInfo;

      let o = {
        phone: {
          value:userInfo.phone,
          reg: /^1[3-9]\d{9}$/,
          errorMsg: "手机格式不正确",
        },
        password: {
          value:userInfo.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          errorMsg: "密码格式不正确",
        },
      };
      //验证表单
      if (!validForm.valid(o)) {
        return;
      }

      // 验证通过
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      //复制userInfo
      let info = Object.assign({}, userInfo);
      info.appkey = this.appkey;

      //参数序列化
      let data = utils.queryString(info);

      this.axios({
        method: "POST",
        url: "/login",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          
          if (result.data.code == 200) {
            // localStorage.setItem("CSTK", result.data.token);
            localStorage.setItem('CSTK', result.data.token);
            this.$router.push({ name: "Home" });
          } else {
            this.$notify({
              type: "warning",
              message: result.data.msg,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
  },
};
</script>

